import { sendAjax } from '@/common/js/sendAjax.js';
import config from '@/apiConfig';
const {getCleanerDeail} = config.api;
export default {
	data() {
		return {
			modalName: null,
			data:[],
			cleanerDetail:[],
			sum:0
		};
	},
	methods: {
		onLoad: function(e) {
			console.log(e)
			this.data = JSON.parse(e.data)
			console.log(this.data)
			this.getCleanerDeail();
			this.calculation();
		},
		calculation(){
			var num = this.data.area;
			var cleanType = this.data.clearnType;
			var billingType = this.data.billingType;
			var unitPrice = 0;
			if(billingType == 1){
				if(cleanType == '常规保洁'){
					unitPrice = 1.5;
				}else if(cleanType == '深度保洁'){
					unitPrice = 5;
				}else if(cleanType == '新居开荒'){
					unitPrice = 7;
				}else if(cleanType == '办公室店面保洁'){
					unitPrice = 1.5
				}
			}else{
				if(cleanType == '常规保洁'){
					unitPrice = 40;
				}else if(cleanType == '深度保洁'){
					unitPrice = 55;
				}else if(cleanType == '新居开荒'){
					unitPrice = 65;
				}else if(cleanType == '办公室店面保洁'){
					unitPrice = 40
				}
			}
			
			this.sum = num * unitPrice; 
		},
		getCleanerDeail(){
			var that = this;
			console.log(this.data.clearnerId)
			let infoOpt = {
				url: getCleanerDeail,
				type: 'POST',
				data: {
					id:this.data.clearnerId
				}
			};
			let infoCb = {};
			infoCb.success = function(res) {
				console.log(res)
				that.cleanerDetail = res;
				uni.hideLoading()
			},
			infoCb.beforeSend = () => {
			  uni.showLoading({
			  	title:'加载中'
			  })
			};
			sendAjax(infoOpt, infoCb);
		},
		//下一页
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		toSub() {
			uni.navigateTo({
				url: '../subscribeSub/subscribeSub'
			});
		}
	}
};
